import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddMedicineService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {
        console.log("Add Measurement Service");
    }

    getAllCategoryValues(categoryId) {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getCategoriesByChemistId?chemistId=" + categoryId, options)
            .map(res => res.json());

    }
    getAllSubCategoryValues(chemistId, categoryId) {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getSubCategoriesByChemistIdByCategoryId?chemistId=" + chemistId + "&categoryId=" + categoryId, options)
            .map(res => res.json());
    }
    getAllDosageValues() {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllChemistUnits", options)
            .map(res => res.json());

    }
    getSubcategoryByCategoryID(chemistId, categoryId) {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getSubCategoriesByChemistIdByCategoryId?chemistId=" + chemistId + "&categoryId=" + categoryId, options)
            .map(res => res.json());
    }
    getAllMeasurementValues() {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllChemistMeasurements", options)
            .map(res => res.json());


    }
    addMedicineAndDosageService(SingleMedicineModel, PricePerQuantityModel) {
        let body = JSON.stringify({ PictureURL: SingleMedicineModel.Url, ChemistId: SingleMedicineModel.ChemistId, ManufacturedBy: SingleMedicineModel.ManufacturedBy, DosageSizeAndValues: SingleMedicineModel.DosageSizeAndValues, Name: SingleMedicineModel.MedicineName, MeasurementIds: SingleMedicineModel.MeasurementModel, SubCategoryId: SingleMedicineModel.SubCategoryId, AddProductDaosageAndSizeMappingDTO: PricePerQuantityModel });
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addProduct", body, options)
            .map(res => res.json());
    }
    addImage(file) {
        let formData: FormData = new FormData();
        formData.append("file", file, file.name);
        let options = new RequestOptions({ method: 'post' });

        return this.http.post(this.urlService.baseUrl + "product/uploadProductImage", formData, options)
            .map(res => res.json());
    }
    addMedicine(Name, ManufacturedBy, MeasurementIds, AddProductDaosageAndSizeMappingDTO, SubCategoryId, ChemistId, PictureURL) {
        let body = JSON.stringify({ Name: Name, ManufacturedBy: ManufacturedBy, MeasurementIds: MeasurementIds, AddProductDaosageAndSizeMappingDTO: AddProductDaosageAndSizeMappingDTO, SubCategoryId: SubCategoryId, ChemistId: ChemistId, PictureURL: PictureURL });
        // console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addProduct", body, options)
            .map(res => res.json());
    }
}


